

export function reformatData() {
    try {
        Excel.run(function (context) {
            // Access the active worksheet
            var activeSheet = context.workbook.worksheets.getActiveWorksheet();

            // Define the mapping between old headers and new headers
            var headerMapping = {
                "Donor Ref for S'Force": "Contact_1_TFN_ID_COPY__C",
                "First Name/Org": "NPSP__CONTACT1_FIRSTNAME__C",
                "Surname": "NPSP__CONTACT1_LASTNAME__C",
                "D": "NPSP__DONATION_CAMPAIGN_NAME__C",
                "E": "DONATION_PROJECT__C",
                "F": "NPSP__DONATION_NAME__C",
                "Paid? (Paid/Blank)": "NPSP__DONATION_STAGE__C",
                "Payment Type for Salesforce": "Donation_Donation_Type__c",
                "I": "Donation_Donation_Amount__c",
                "Gift Aid": "Donation_Gift_Aid_Declaration__c",
                "K": "NPSP__DONATION_AMOUNT__C",
                "Payment Date": "NPSP__DONATION_DATE__C",
                "M": "NPSP__DONATION_RECORD_TYPE_NAME__C",
                "Company Name": "Organisation",
                "Donation Donor": "Donation Donor",
            };

            // Get the range of headers and data
            var getRang = activeSheet.getUsedRange();
            var getValue = getRang.load("values");

            // Execute the queued commands
            return context.sync()
                .then(function () {
                    // Create a new worksheet or get existing "Reformat" sheet
                    var newSheet = context.workbook.worksheets.getItemOrNullObject("Reformat");
                    newSheet.load("isNullObject");

                    return context.sync()
                        .then(function () {
                            if (newSheet.isNullObject) {
                                newSheet = context.workbook.worksheets.add("Reformat");
                            } else {
                                newSheet.getUsedRange().clear();
                            }
                            newSheet.activate();

                            // Write new headers to the new sheet
                            newSheet.getRange("A1:O1").values = [Object.values(headerMapping)];

                            // Get the data rows
                            var data = getValue.values;
                            var CampaignName = getValue.values[0][0]; // use index for the Campaign Name

                            // Initialize row index for new sheet
                            var newDataRowIndex = 2;

                            // Process each row
                            data.forEach(function (dataRow, rowIndex) {
                                if (rowIndex >= 10) { // Start from row 10
                                    var secondHeaderRow = data[9];
                                    var firstHeaderRow = data[8];
                                    var firstName = dataRow[1];
                                    var surName = dataRow[2];
                                    var getDate = dataRow[19];
                                    var salesForce = dataRow[24];

                                    // Skip rows where salesForce (case-insensitive) is "y"
                                    if (typeof salesForce === 'string' && salesForce.trim().toLowerCase() === 'y') {
                                        return;
                                    }

                                    // var formattedDate = '';
                                    // if (!isNaN(parseInt(getDate))) {
                                    //     var serialDateNumber = parseInt(getDate);
                                    //     var dateValue = new Date((serialDateNumber - 25569) * 86400 * 1000);
                                    //     formattedDate = dateValue.getDate() + '/' + (dateValue.getMonth() + 1) + '/' + dateValue.getFullYear();
                                    // }
                                    var getcolFDate = dataRow[19];
                                    var newDate = '';
                                    if (!isNaN(parseInt(getcolFDate))) {
                                        var serialDateNumber = parseInt(getcolFDate);
                                        var newDateValue = new Date((serialDateNumber - 25569) * 86400 * 1000);
                                        newDate = newDateValue.getDate() + '/' + (newDateValue.getMonth() + 1) + '/' + newDateValue.getFullYear();
                                    }

                                    var getDate = dataRow[19];
                                    var formattedDate = '';
                                    if (!isNaN(parseInt(getDate))) {
                                        var serialDateNumber = parseInt(getDate);
                                        var dateValue = new Date((serialDateNumber - 25569) * 86400 * 1000);

                                        // Use Intl.DateTimeFormat to format the date in a long format
                                        var formatter = new Intl.DateTimeFormat('en-US', {
                                            // weekday: 'long',
                                            year: 'numeric',
                                            month: 'short',
                                            day: 'numeric'
                                        });

                                        formattedDate = formatter.format(dateValue);
                                    }

                                    console.log(formattedDate); // Example output: "Thursday, August 15, 2024"


                                    // Map and write corresponding data to the new sheet
                                    for (var i = 6; i < 14; i++) {
                                        if (dataRow[i]) {
                                            var newDataRow = [];
                                            for (var oldHeader in headerMapping) {
                                                var oldHeaderIndex = secondHeaderRow.indexOf(oldHeader);
                                                var newHeaderIndex = Object.values(headerMapping).indexOf(headerMapping[oldHeader]);

                                                if (headerMapping[oldHeader] === "DONATION_PROJECT__C") {
                                                    newDataRow[newHeaderIndex] = firstHeaderRow[i];
                                                } else if (headerMapping[oldHeader] === "NPSP__DONATION_CAMPAIGN_NAME__C") {
                                                    newDataRow[newHeaderIndex] = CampaignName;
                                                } else if (headerMapping[oldHeader] === "NPSP__DONATION_NAME__C") {
                                                    newDataRow[newHeaderIndex] = firstName + " " + surName + " - Donation to " + secondHeaderRow[i] + " - " + formattedDate;
                                                } else if (headerMapping[oldHeader] === "Donation_Donation_Amount__c" || headerMapping[oldHeader] === "NPSP__DONATION_AMOUNT__C") {
                                                    newDataRow[newHeaderIndex] = dataRow[i];
                                                } else if (headerMapping[oldHeader] === "NPSP__DONATION_RECORD_TYPE_NAME__C") {
                                                    newDataRow[newHeaderIndex] = "Project Donation";
                                                } else if (headerMapping[oldHeader] === "NPSP__DONATION_DATE__C") {
                                                    newDataRow[newHeaderIndex] = formattedDate;
                                                } else {
                                                    newDataRow[newHeaderIndex] = dataRow[oldHeaderIndex];
                                                }
                                            }
                                            newSheet.getRange("A" + newDataRowIndex + ":O" + newDataRowIndex).values = [newDataRow];
                                            newDataRowIndex++; // Move to the next row
                                        }
                                    }
                                }
                            });

                            // Auto-fit columns after adding data
                            newSheet.getUsedRange().format.autofitColumns();
                        });
                }).catch(function (error) {
                    console.log(error);
                });
        });
    } catch (error) {
        console.log(error);
    }
}